<template>
	<view class="logistics">
		<!-- 快递信息 -->
		<view class="express">
			<!-- 快递头像 -->
			<view class="img">
				<image src="../../static/img/express1@2x.png" mode="aspectFit"></image>
			</view>
			<!-- 快递详情 -->
			<view class="info">
				<!-- 快递名称 -->
				<view class="type">
					{{company}}
				</view>
				<!-- 订单号 -->
				<view class="order">
					<text class="num">单号 {{number}}</text>
					<text class="copy" @click="copy(number)">复制</text>
				</view>
			</view>
		</view>
		<!-- 收货地址 -->
		<view class="address">
			<image src="../../static/img/shipping.png" mode="aspectFit"></image>
			<text>收货地址： {{address}}</text>
		</view>
		<!-- 物流详情 -->
		<view class="detail">
			<!-- <uni-steps :options="[
				{title:' 广东省广州市天河区汇彩公司 已签收：他人代收感谢使用圆通快递，期待再次为您服务',desc:'2019-11-21 17:41:13'},
				{title:'广东省广州市天河区汇彩公司 吴远木 （152175436978）正在派件',desc:'2019-11-21 17:41:13'},
				{title:'广东省广州市天河区汇彩   以收入',desc:'2019-11-21 17:41:13'},
				{title:'广州转运中心 已发出  下一站 广东省广州市',desc:'2019-11-21 17:41:13'},
				{title:'广东省广州市天河区汇彩公司 吴远木 （152175436978）已收件',desc:'2019-11-21 17:41:13'},
				{title:'您的订单已倒入，商家正通知快递公司取件',desc:'2019-11-21 17:41:13'}]"
			 direction="column" :active="active" active-color="#ff594d">
			</uni-steps> -->

		</view>
	</view>
</template>

<script>
	import service from '@/store/service.js';
	import uniSteps from '@/components/uni-steps/uni-steps.vue'
	export default {
		data() {
			return {
				active:5,
				oid:0,
				company:'',
				number:'',
				address:''
			}
		},
		components: {
			uniSteps
		},
		onLoad:function(e){
			console.log(e);
			this.oid=e.oid
			this.getExpressQuery()
		},
		methods:{
			copy(url) {
				uni.setClipboardData({
					data: url,
					success: function(res) {
						uni.showToast({
							icon: 'none',
							title: '复制成功'
						})
					}
				});
			},
			getExpressQuery(oid){
				var self = this;
				// console.log(self.gid,666);
				self.$http
					.post(service.api.order.expressQuery, {
						order_id: self.oid
					})
					.then(function(res) {
						console.log(res);
						var list = res.data.order;
						self.company=list.express_company_title
						console.log(self.company);
						self.number = list.express_send_no;
						self.address=list.express_province+list.express_city+list.express_area+list.express_address
						console.log();
					});
				}
		}
	}
</script>

<style scoped lang="less">
	.logistics {
		width:100%;
		height:100vh;
		overflow: hidden;
		background-color: #FFFFFF;
		// <!-- 快递信息 -->
		.express {
			height: 132upx;
			width: 100%;
			display: flex;
			align-items: center;
			box-sizing: border-box;
			padding-left: 27upx;
			border-bottom: 1px solid rgba(241,241,241,1);

			// <!-- 快递头像 -->
			.img {
				height: 89upx;
				width: 87upx;

				// <!-- 快递详情 -->
				image {
					height: 100%;
					width: 100%;
				}
			}

			.info {
				margin-left: 14upx;

				.type {
					height: 30upx;
					font-size: 29upx;
					font-weight: 500;
					color: rgba(233, 144, 64, 1);
					line-height: 30upx;
				}

				.order {
					.num {
						height: 28upx;
						font-size: 29upx;
