						border: 1px solid rgba(181, 181, 181, 1);
						border-radius: 6upx;
						font-size: 24upx;
						font-weight: 500;
						color: rgba(102, 102, 102, 1);
						padding: 0px 10rpx;
						text-align: center;
						line-height: 42upx;
						margin-left: 17upx;
					}
				}
			}
		}
// 收货地址
		.address {
			height: 100upx;
			width: 100%;
			padding-left: 30upx;
			display: flex;
			align-items: center;
			border-bottom: 1px solid rgba(241,241,241,1);

			>image {
				width: 38upx;
				height: 45upx;
			}

			>text {
				height: 27upx;
				font-size: 28upx;
				font-weight: 500;
				color: rgba(102, 102, 102, 1);
				line-height: 27upx;
			}
		}

		.detail {
			width: 100%;
			padding: 36upx 57upx 0 30upx;
			box-sizing: border-box;
		}
		.uni-steps__column-text{
		border-bottom:0 !important;
	}
	
	}

</style>
